import React from "react";
import { Typography } from "@mui/material";

export default function Meaning(props) {
  return (
    <>
      <div className="meaning">
        <Typography sx={{ mb: 1, fontStyle: "italic" }} color="text.secondary">
          {props.meaning.partOfSpeech}
        </Typography>
        {props.meaning.definitions.map((definition, index) => {
          return (
            <div key={index}>
              <li
                className="meaningLi"
                style={{ marginLeft: "1.5rem", fontSize: "1.2rem" }}
              >
                <Typography sx={{ mb: .2, fontSize: '1rem' }} variant="body2">
                  {definition.definition}
                </Typography>
              </li>
                <Typography sx={{ mb: 1.5, fontSize: '.9rem', marginLeft: '2rem' }} color="text.secondary">
                    {definition.example === undefined ? "" : `"${definition.example}"`}
                </Typography>
            </div>
          );
        })}
      </div>
    </>
  );
}
